*, :after, :before {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, pre, samp {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

[type="button"], [type="reset"], [type="submit"], button {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

body {
  font-family: var(--font-main);
  background-color: var(--bcg-color-one);
}

h1, h2, h3, h4, h5, h6, p, ul {
  font-weight: inherit;
  font-style: inherit;
  vertical-align: baseline;
  border: 0;
  outline: 0;
  margin: 0;
  padding: 0;
  font-family: inherit;
  font-size: 100%;
}

a {
  text-decoration: none;
  display: inline-block;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}

h1, h2, h3, p {
  margin: 0;
}

button {
  cursor: pointer;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    max-width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding-left: 149px;
    padding-right: 149px;
  }
}

:root {
  --primary-color: #2b2b2b;
  --secondary-color: #487996;
  --accent-color: #008cef;
  --accent-color-second: #bdbdbd;
  --bcg-color-one: #fff;
  --bcg-color-two: #e5e5e5;
  --font-main: "Cera Pro", serif;
  --font-second: "Caviar Dreams", serif;
  --duration: 250ms;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

body.menu-open {
  overflow: hidden;
}

header {
  justify-content: center;
  display: flex;
}

header > .container {
  width: 320px;
  z-index: 3;
  flex-grow: 1;
  align-items: center;
  padding: 23px 20px;
  display: flex;
  position: absolute;
}

@media screen and (min-width: 768px) {
  header > .container {
    width: 768px;
    justify-content: space-between;
    padding: 20px 32px;
  }
}

@media screen and (min-width: 1440px) {
  header > .container {
    width: 1440px;
    padding: 40px 32px;
  }
}

.header__logo {
  min-width: 101px;
  margin-right: 23px;
}

@media screen and (min-width: 768px) {
  .header__logo {
    width: 125px;
    margin-right: 220px;
  }
}

@media screen and (min-width: 1440px) {
  .header__logo {
    width: 192px;
    margin-right: 258px;
  }
}

.header-button__phone {
  color: #008cef;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: 31px;
  padding-left: 0;
  padding-right: 0;
}

@media screen and (min-width: 768px) {
  .header-button__phone {
    display: none;
  }
}

.burger-icon {
  flex-shrink: 0;
  display: flex;
}

.header-button__lang {
  letter-spacing: .01em;
  color: #bdbdbd;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  margin-left: 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
}

@media screen and (min-width: 768px) {
  .header-button__lang {
    margin-left: 80px;
  }
}

@media screen and (min-width: 1440px) {
  .header-button__lang {
    margin-left: 140px;
    margin-right: 70px;
  }
}

.header-phone {
  display: none;
}

@media screen and (min-width: 768px) {
  .header-phone {
    flex-direction: column;
    gap: 10px;
    margin-left: auto;
    display: flex;
  }
}

@media screen and (min-width: 1440px) {
  .header-phone {
    width: 171px;
  }
}

.phone-modal {
  z-index: 4;
  width: 180px;
  height: 82px;
  background-color: #fff;
  border-radius: 2px;
  margin-top: 65px;
  padding: 16px 6px;
  position: absolute;
  top: 0;
  right: 0;
}

.phone-modal__list {
  flex-direction: column;
  align-items: center;
  gap: 10px;
  display: flex;
}

.phone-modal__item:before {
  content: "";
  border: 5px solid rgba(0, 0, 0, 0);
  border-top-color: #fff;
  margin-left: -5px;
  position: absolute;
  bottom: 100%;
  left: 50%;
  rotate: 180deg;
}

.icon-call {
  margin-right: 4px;
}

.s {
  letter-spacing: .01em;
  color: var(--primary-color);
  transition: color var(--duration) var(--timing-function);
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  display: flex;
}

.s:hover, .s:focus {
  color: var(--accent-color);
}

.drop-down {
  display: flex;
}

.menu-desktop {
  display: none;
}

@media screen and (min-width: 1440px) {
  .menu-desktop {
    display: flex;
  }
}

.polygon-icon {
  color: #008cef;
  margin-left: 4px;
}

.menu-desktop__list {
  flex-direction: row;
  gap: 56px;
  display: flex;
}

.menu-desktop__item {
  font-family: var(--font-second);
  letter-spacing: .01em;
  text-align: center;
  color: var(--primary-color);
  font-size: 18px;
  font-weight: 400;
  line-height: 1.17;
}

.menu-desktop__item--dropdown {
  transition: opacity .9s;
}

.menu-desktop__item--dropdown:hover .sub-menu__list {
  opacity: 1;
}

.menu-desktop__link {
  color: var(--primary-color);
  transition: color var(--duration) var(--timing-function);
}

.menu-desktop__link:hover, .menu-desktop__link:focus {
  color: var(--accent-color);
}

.menu-desktop__arrow {
  width: 0;
  height: 0;
  border-top: 5px solid #008cef;
  border-left: 7px solid rgba(0, 0, 0, 0);
  border-right: 7px solid rgba(0, 0, 0, 0);
  position: absolute;
  top: 65px;
  right: 748px;
}

.sub-menu__list {
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
  position: absolute;
  top: 82px;
  left: 575px;
}

.sub-menu__item:not(:last-child) {
  margin-bottom: 4px;
}

.sub-menu__link {
  color: var(--primary-color);
  letter-spacing: .01em;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.19;
}

.sub-menu__link:hover, .sub-menu__link:focus {
  color: var(--accent-color);
}

.menu-open .pattern-wrap {
  z-index: 3;
}

.menu-open__btn {
  width: 42px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  flex-direction: column;
  justify-content: center;
  display: flex;
}

@media screen and (min-width: 1440px) {
  .menu-open__btn {
    display: none;
  }
}

.line {
  width: 33px;
  padding: 2px;
  display: block;
}

.line:after {
  content: "";
  background: var(--secondary-color);
  width: 100%;
  height: 2px;
  border-radius: 2px;
  display: block;
}

.mob-menu {
  z-index: 4;
  width: 100vw;
  height: 100vh;
  background-color: #fff;
  margin-top: 90px;
  padding: 20px;
  position: fixed;
  top: 0;
  left: 0;
}

@media screen and (min-width: 768px) {
  .mob-menu {
    padding: 40px;
  }
}

.mob-menu__list {
  flex-direction: column;
  align-items: center;
  gap: 24px;
  display: flex;
}

.mob-menu__item--sub {
  position: relative;
}

.mob-menu__item--sub:before {
  content: "";
  width: 7px;
  height: 7px;
  background: #487996;
  border-radius: 3px 0;
  position: absolute;
  top: 12px;
  left: -20px;
}

.mob-menu__link {
  text-align: center;
  color: var(--secondary-color);
  transition: color var(--duration) var(--timing-function);
  font-size: 24px;
  font-weight: 700;
  line-height: 1.25;
}

.mob-menu__link:hover, .mob-menu__link:focus {
  color: var(--accent-color);
}

.mob-menu__link--sub {
  text-align: center;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.25;
}

.menu-close {
  border: none;
}

